function catalogSliders() {
    let SlidersArray = []
    $(".catalog-card").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.catalog-card[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            loop: true,
            navigation: {
                nextEl: `.catalog-card[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.catalog-card[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    loop: false,
                },
                992: {
                    loop: true,
                }
            }
        })
    })
}

catalogSliders()
